import Taro from '@tarojs/taro';
import { API_CONFIG } from '@/config/api';

interface RequestOptions {
  method?: 'GET' | 'POST' | 'PUT' | 'DELETE';
  data?: any;
  header?: Record<string, string>;
  showLoading?: boolean;
  loadingTitle?: string;
}

interface ApiResponse<T = any> {
  code: number;
  message: string;
  data: T;
}

/**
 * ç»Ÿä¸€è¯·æ±‚å°è£…
 * @param url - APIè·¯å¾„
 * @param options - è¯·æ±‚é€‰é¡¹
 * @returns Promise<ApiResponse>
 */
export async function request<T = any>(
  url: string,
  options: RequestOptions = {}
): Promise<ApiResponse<T>> {
  const {
    method = 'GET',
    data,
    header = {},
    showLoading = false,
    loadingTitle = 'åŠ è½½ä¸­...'
  } = options;

  // æ˜¾ç¤ºåŠ è½½æç¤º
  if (showLoading) {
    Taro.showLoading({ title: loadingTitle, mask: true });
  }

  try {
    const response = await Taro.request({
      url: `${API_CONFIG.baseURL}${url}`,
      method,
      data,
      header: {
        'Content-Type': 'application/json',
        ...header
      },
      timeout: API_CONFIG.timeout
    });

    // éšè—åŠ è½½æç¤º
    if (showLoading) {
      Taro.hideLoading();
    }

    // å¤„ç†å“åº”
    const result = response.data as ApiResponse<T>;

    // ä¸šåŠ¡é”™è¯¯å¤„ç†
    if (result.code !== 0) {
      console.error(`APIä¸šåŠ¡é”™è¯¯: ${url}`, result);
      throw new Error(result.message || 'è¯·æ±‚å¤±è´¥');
    }

    return result;
  } catch (error: any) {
    // éšè—åŠ è½½æç¤º
    if (showLoading) {
      Taro.hideLoading();
    }

    // ç½‘ç»œé”™è¯¯å¤„ç†
    console.error(`APIç½‘ç»œé”™è¯¯: ${url}`, error);

    // æŠ›å‡ºé”™è¯¯ä¾›ä¸Šå±‚å¤„ç†
    throw error;
  }
}

/**
 * GETè¯·æ±‚
 */
export const get = <T = any>(url: string, params?: any, options?: RequestOptions) => {
  // æ„å»ºæŸ¥è¯¢å­—ç¬¦ä¸²
  const queryString = params ?
    '?' + Object.keys(params)
      .filter(key => params[key] !== undefined && params[key] !== null)
      .map(key => `${key}=${encodeURIComponent(params[key])}`)
      .join('&') : '';

  return request<T>(url + queryString, { ...options, method: 'GET' });
};

/**
 * POSTè¯·æ±‚
 */
export const post = <T = any>(url: string, data?: any, options?: RequestOptions) => {
  return request<T>(url, { ...options, method: 'POST', data });
};

/**
 * PUTè¯·æ±‚
 */
export const put = <T = any>(url: string, data?: any, options?: RequestOptions) => {
  return request<T>(url, { ...options, method: 'PUT', data });
};

/**
 * DELETEè¯·æ±‚
 */
export const del = <T = any>(url: string, data?: any, options?: RequestOptions) => {
  return request<T>(url, { ...options, method: 'DELETE', data });
};